import React from 'react';

import styles from './radarTable.module.css';

import { useStore } from "../../../../store/root_store";
import { observer } from "mobx-react-lite";
import net from "../../../../net/network";

import {Button,InputGroup,FormControl} from "react-bootstrap";
import { vectorEndCoords } from "helpers/mathHelpers.js";
import { v4 as uuidv4 } from 'uuid';


const RadarAddEl = observer(() => { 
    let radar = useStore().pilotData;
    const inpChange = (field)=>(event) =>{ radar.setRadarElem(field,event.target.value)};
    const inpNumChange = (field)=>(event) =>{if(event.target.value.match(/^(\s*|\d+)$/)) radar.setRadarElem(field,Number(event.target.value))};
    const inpBoolChange = (field)=>(event) =>{radar.setRadarElem(field,event.target.checked)};
    
    const inpDirectionChange = (event) =>{
        let tmpValue=event.target.value;
        if(tmpValue.match(/^(\s*|\d+)$/)){
            radar.setRadarElem("direction",event.target.value);
            let tmpDir= tmpValue ? tmpValue : 0;
            let tmpCoords = vectorEndCoords(0,0,tmpDir,550);
            radar.setRadarElem("x",Math.round(tmpCoords[0]));
            radar.setRadarElem("y",Math.round(tmpCoords[1]));
        } 
    };

    let userId = useStore().user.id;
  
    let radarAddElem=()=>{
        let tmpData={
            "menu":"pilot",
            "action":"radarAddEl",
            "data": {
                id: uuidv4(),
                timeStamp:0,
                name: radar.radarElem.name ? radar.radarElem.name : "unknown",
                type: radar.radarElem.type ? radar.radarElem.type : "unknown",
                eA: radar.radarElem.angle ,
                eX: radar.radarElem.x,
                eY: radar.radarElem.y,
                speed: radar.radarElem.speed,
                size: radar.radarElem.size,
                initialPause: radar.radarElem.pause,
                transponder:radar.radarElem.transponder
            }
        }
        // console.log("ToSend: ",JSON.stringify(tmpData))
        net.netPostAction(userId,tmpData);
    }

    let radarSetDefaultElem=()=>{
        radar.setRadarElem("name", "unknown");
        radar.setRadarElem("type", "unknown");
        radar.setRadarElem("angle", 180);
        radar.setRadarElem("direction", 0);
        radar.setRadarElem("x", 0);
        radar.setRadarElem("y", 550);
        radar.setRadarElem("speed", 50);
        radar.setRadarElem("size", 25);
        radar.setRadarElem("pause", 6);
        radar.setRadarElem("transponder", false);
    }

    return (
        <div className={styles.systemsDiv}>
            <div className={styles.systemsHeader}>
                <p>Имя:</p>
                <div className={styles.systemsHeaderInput}>
                        <FormControl style={{"fontSize":"20px"}} value={radar.radarElem.name} onChange={inpChange("name")}/> 
                </div>
                <p>Тип:</p>
                <div className={styles.systemsHeaderInput}>
                        <FormControl style={{"fontSize":"20px"}} value={radar.radarElem.type} onChange={inpChange("type")}/> 
                </div>
                <p>Направ.:</p>
                <div className={styles.systemsHeaderInput}>
                        <FormControl style={{"fontSize":"20px"}} value={radar.radarElem.direction} onChange={inpDirectionChange}/> 
                </div>
                <p>Угол:</p>
                <div className={styles.systemsHeaderInput}>
                        <FormControl style={{"fontSize":"20px"}} value={radar.radarElem.angle} onChange={inpNumChange("angle")}/> 
                </div>
                <p>Размер:</p>
                <div className={styles.systemsHeaderInput}>
                        <FormControl style={{"fontSize":"20px"}} value={radar.radarElem.size} onChange={inpNumChange("size")}/> 
                </div>
                <p>Скорость:</p>
                <div className={styles.systemsHeaderInput}>
                        <FormControl style={{"fontSize":"20px"}} value={radar.radarElem.speed} onChange={inpNumChange("speed")}/> 
                </div>
            </div>
            <div className={styles.systemsHeader}>
                <p>Пауза:</p>
                    <div className={styles.systemsHeaderInput}>
                            <FormControl style={{"fontSize":"20px"}} value={radar.radarElem.pause} onChange={inpNumChange("pause")}/> 
                    </div>
                <p>Транспондер:</p>
                <div className={styles.systemsHeaderInput}>
                        <InputGroup.Checkbox checked={radar.radarElem.transponder} onChange={inpBoolChange("transponder")}/>
                        {/* <FormControl style={{"fontSize":"20px"}} value={radar.radarElem.transponder} onChange={inpNumChange("pause")}/>  */}
                </div>
                <p>X:<span className={styles.systemsButtonText}>{radar.radarElem.x}</span> </p>
                <p>Y:<span className={styles.systemsButtonText}>{radar.radarElem.y}</span> </p>           
            </div>
            <Button variant={"primary"} onClick={radarAddElem}><span className={styles.systemsButtonText}>Добавить</span></Button>
            <Button variant={"warning"} onClick={radarSetDefaultElem}><span className={styles.systemsButtonText}>Default</span></Button>     
        </div>
    );
  })


  const AstroChangeElements = observer(() => { 
    let pilotData = useStore().pilotData;
    let {astro} = pilotData.data;
    if(!astro) return ("");
    const inpChange = (field)=>(event) =>{ pilotData.setOneElement(field,event.target.value)};
    const inpNumChange = (field)=>(event) =>{if(event.target.value.match(/^(\s*|\d+)$/)) pilotData.setOneElement(field,Number(event.target.value))};
    // const inpBoolChange = (field)=>(event) =>{radar.setRadarElem(field,event.target.checked)};
    
    let userId = useStore().user.id;
  
    let astroSetTimeLeft=()=>{
        let tmpData={
            "menu":"pilot",
            "action":"astroTimeAdd",
            "data": pilotData.astroTimeLeft
        }
        // console.log("ToSend: ",JSON.stringify(tmpData))
        net.netPostAction(userId,tmpData);
    }

    let astroSetSystem=()=>{
        let tmpData={
            "menu":"pilot",
            "action":"astroSetSytem",
            "data": pilotData.astroSetCurrent
        }
        // console.log("ToSend: ",JSON.stringify(tmpData))
        net.netPostAction(userId,tmpData);
    }

    return (
        <div className={styles.systemsDiv}>
            <div className={styles.systemsHeader}>
                <p>Время перехода: {astro.timeLeft*10} Переход: {astro.voyage ? "true" : "false"}</p>
                <div className={styles.systemsHeaderInput}>
                        <FormControl style={{"fontSize":"20px"}} value={pilotData.astroTimeLeft} onChange={inpNumChange("astroTimeLeft")}/> 
                </div>
                <Button variant={"primary"} onClick={astroSetTimeLeft}>
                    <span className={styles.systemsButtonText}>Установить</span>
                </Button>
            </div>   
            <div className={styles.systemsHeader}>
                <p>Система: {astro.current} Курс: {astro.course ? astro.course  : "нет"}</p>
                <div className={styles.systemsHeaderInput}>
                        <FormControl style={{"fontSize":"20px"}} value={pilotData.astroSetCurrent} onChange={inpChange("astroSetCurrent")}/> 
                </div>
                <Button variant={"primary"} onClick={astroSetSystem}>
                    <span className={styles.systemsButtonText}>Установить</span>
                </Button>
            </div>
        </div>
    );
  })




const SystemActionButton = ({state=false,text=["1","2"],variant=["primary","secondary"],callBack}) => { 
  return (
      <Button 
          className={styles.actionButton}
          variant={state ? variant[0] : variant[1]}
          onClick={callBack}
      >
          <p className={styles.actionButtonText}>{state ? text[0] : text[1]}</p>
      </Button>
  );
}

let RadarTable = observer(()=>{
    let radar = useStore().pilotData.data.radar;
    let pilotData = useStore().pilotData;
    let userId = useStore().user.id;
  
    let radarRem=(name)=>{
      let tmpData={
          "menu":"pilot",
          "action":"radarRemoveEl",
          "data": name
      }
      net.netPostAction(userId,tmpData);
  }

    let radarAttack=(name)=>{
        let tmpData={
            "menu":"pilot",
            "action":"radarAttack",
            "data": name
        }
        net.netPostAction(userId,tmpData);
    }

    let radarEvade=(name)=>{
        let tmpData={
            "menu":"pilot",
            "action":"radarEvade",
            "data": name
        }
        net.netPostAction(userId,tmpData);
    }
  return (
    <div>
        <RadarAddEl radar={pilotData}/>
        <table className={styles.systemsTable}>
        <tbody>
            <RadarTableHeader />
            {   
                radar.map((el,idx)=>(
                    <RadarTableRow 
                        key={el.name} 
                        data={el}
                        idx={idx} 
                        remCallBack={radarRem} 
                        attackCallBack={radarAttack}
                        evadeCallBack={radarEvade}
                        
                        />
                ))
            } 
        </tbody>
        </table>


    </div>
  )
})

const RadarTableHeader = () => { 
  let backgroundColor = "blue";
  let data=["Название","Тип","X","Y","Угол","Размер","Cкорость","До появления","Тр.","Убрать"];
  return (
      <>
          <tr className={styles.tableRow}>
              {data.map((el,cellIdx)=>(
                  <td className={`${styles[backgroundColor]} ${styles.tableElement}`} key={cellIdx}>
                      <p className={`${styles.tableText} ${styles.boldText}`}>{ el }</p>
                  </td> 
              ))}
          </tr>
    </>
  );
}

const RadarTableRow = ({idx,data,bold=false,remCallBack,attackCallBack,evadeCallBack}) => { 
  let backgroundColor = idx%2 ? "blue" : "gray";
  let boldText= bold ? "boldText" : "";
  let localData=[data.name,data.type,data.eX,data.eY,data.eA,data.size,data.speed,data.initialPause,data.transponder ? "true" : "false"];
  console.log("Row ",localData);
  return (
      <>
          <tr className={styles.tableRow}>
              {localData.map((el,cellIdx)=>(
                  <td className={`${styles[backgroundColor]} ${styles.tableElement}`} key={cellIdx}>
                      <p className={`${styles.tableText} ${styles[boldText]}`}>{typeof el === "number" ? Math.round(el) : el}</p>
                  </td>
              ))}
              <td className={`${styles[backgroundColor]} ${styles.tableElement}`}>
                  { <SystemActionButton 
                      state={true}
                      text={["Удалить",""]}
                      variant={["warning","secondary"]}   
                      callBack={()=>remCallBack(data.name)} 
                  />}
              </td>
              <td className={`${styles[backgroundColor]} ${styles.tableElement}`}>
                <Button variant={"danger"} onClick={()=>{attackCallBack(data.id)}}>
                    <span className={styles.systemsButtonText}>Атаковать</span>
                </Button>
              </td>
              <td className={`${styles[backgroundColor]} ${styles.tableElement}`}>
                <Button variant={"primary"} onClick={()=>{evadeCallBack(data.id)}}>
                    <span className={styles.systemsButtonText}>Убежать</span>
                </Button>
              </td>
          </tr>
    </>
  );
}

export {
    RadarTable,
    AstroChangeElements

};
