import React, { useState } from 'react';
import styles from './systems.module.css';

import { useStore } from "../../../store/root_store";
import { observer } from "mobx-react-lite";
import net from "../../../net/network";

import SystemImageWidget from '../systemButton/systemButton';
import {Button,InputGroup,FormControl} from "react-bootstrap";

const GameSystems=observer( ()=>{
    let systems=useStore().systems;
    let userId=useStore().user.id;
    console.log("systems: ",systems.cycle);

    let switchStateClb=(system,state)=>{
        
        let action;
        switch(state){
            case "switchedOn":
                action= system.switchedOn ? "switchOff" : "switchOn";
                break;
            case "broken":
                action= system.broken ? "brokenOff" : "brokenOn";
                break;
            case "hidden":
                action= system.hidden ? "hiddenOff" : "hiddenOn";
                break;
            default:
                return "";
        }
    
        let tmpData={
            "menu":"systems",
            "action":action,
            "data": system.name
        }
        net.netPostAction(userId,tmpData);
    }
   
    return (
    <div className={styles.systemsDiv}>
        <SystemsHeader systems={systems}/>
        <table className={styles.systemsTable}>
            <tbody>
                <SystemTableHeader />
                {   
                    systems.data.map((el,idx)=>(
                        <SystemTableRow 
                            key={el.name} data={el} idx={idx} callBack={switchStateClb}/>
                    ))
                } 
              
            </tbody>
        </table>
        <FuelChange/>
    </div>
  )
});

const SystemsHeader = ({systems}) => { 
    const [inpText,setInpText]=useState(0);
    let userId=useStore().user.id;
    const inpChange = (event) =>{ if(event.target.value.match(/^(\s*|\d+)$/)) setInpText(event.target.value)};
    let fuelChangeClb=(operation)=>{
        let chgAmount=0;
        let mult = operation ? 1 : -1;
        if(inpText) chgAmount=Number(inpText);
        if(chgAmount){                    
            let tmpData={
                "menu":"systems",
                "action":"changeFuel",
                "data": chgAmount*mult
            }
            console.log("Operation: ",tmpData);
            net.netPostAction(userId,tmpData);
        }
    }

    let switchCycleClb=()=>{
        let action= systems.cycle ? "cycleOff" : "cycleOn";
        let tmpData={
            "menu":"systems",
            "action":action,
            "data": ""
        }
        net.netPostAction(userId,tmpData);
    }

    return(
        <div className={styles.systemsHeader}>
            <p>Топливо: {systems.data.find(el=>el.name==="fuel1").fuelStorage}</p>
            <div className={styles.systemsHeaderInput}>
                <InputGroup >
                    <InputGroup.Prepend>
                        <Button variant="outline-danger" onClick={()=>fuelChangeClb(false)}>
                            <span className={styles.systemsButtonText}>-</span>
                        </Button>
                    </InputGroup.Prepend>
                    <FormControl style={{"fontSize":"20px"}} value={inpText} onChange={inpChange}/>
                    <InputGroup.Append>
                        <Button variant="outline-primary"  onClick={()=>fuelChangeClb(true)}>
                            <span className={styles.systemsButtonText}>+</span>
                        </Button>
                    </InputGroup.Append>
                </InputGroup>
            </div>
            <p>Энергия: {systems.powerLevel}</p>
            <SystemActionButton 
                state={systems.cycle} 
                variant={["success","secondary"]} 
                text={["Цикл идет","Цикл выкл."]} 
                callBack={switchCycleClb}/>
        </div>
    )
}

const SystemActionButton = ({state=false,text=["1","2"],variant=["primary","secondary"],callBack}) => { 
    return (
        <Button 
            className={styles.actionButton}
            variant={state ? variant[0] : variant[1]}
            onClick={callBack}
        >
            <p className={styles.actionButtonText}>{state ? text[0] : text[1]}</p>
        </Button>
    );
}



const SystemTableRow = ({idx,data,bold=false,callBack}) => { 
    let backgroundColor = idx%2 ? "blue" : "gray";
    let boldText= bold ? "boldText" : "";
    let localData=[data.screenName,data.power,data.fuelConsumption,data.effectiveness,data.descr]
    return (
        <>
            <tr className={styles.tableRow}>
                <td className={`${styles[backgroundColor]} ${styles.tableElement}`}>
                    <SystemImageWidget system={data}/>
                </td>
                <td className={`${styles[backgroundColor]} ${styles.tableElement}`}>
                    <SystemActionButton 
                        state={data.switchedOn}
                        text={["Включен","Выключен"]}
                        variant={["success","secondary"]}
                        callBack={()=>callBack(data,"switchedOn")}    
                    />
                </td>
                <td className={`${styles[backgroundColor]} ${styles.tableElement}`}>
                    <SystemActionButton 
                        state={data.broken}
                        text={["Сломан","Исправен"]}
                        variant={["danger","secondary"]}  
                        callBack={()=>callBack(data,"broken")}                     
                    />
                </td>
                <td className={`${styles[backgroundColor]} ${styles.tableElement}`}>
                    <SystemActionButton 
                        state={data.hidden}
                        text={["Спрятан","Виден"]}
                        variant={["warning","secondary"]}   
                        callBack={()=>callBack(data,"hidden")} 
                    />
                </td>

                {localData.map((el,cellIdx)=>(
                    <td className={`${styles[backgroundColor]} ${styles.tableElement}`} key={cellIdx}>
                        <p className={`${styles.tableText} ${styles[boldText]}`}>{el}</p>
                    </td>
                ))}
            </tr>
      </>
    );
}

const SystemTableHeader = () => { 
    let backgroundColor = "blue";
    let data=["","Вкл./Выкл.","Состояние","Скрыт","Название","Энергия","Потребл.топлива","Эффект.","Описание"];
    return (
        <>
            <tr className={styles.tableRow}>
                {data.map((el,cellIdx)=>(
                    <td className={`${styles[backgroundColor]} ${styles.tableElement}`} key={cellIdx}>
                        <p className={`${styles.tableText} ${styles.boldText}`} >{el}</p>
                    </td>
                ))}
            </tr>
      </>
    );
}


const FuelChange = observer(() => { 
    let systems = useStore().systems;
    let userId = useStore().user.id;

    // const inpChange = (field)=>(event) =>{ pilotData.setOneElement(field,event.target.value)};
    const inpNumChange = (field)=>(event) =>{if(event.target.value.match(/^(\s*|\d+)$/)) systems.setOneElement(field,Number(event.target.value))};
    
    let fuelMaxLevelSend=()=>{
        let tmpData={
            "menu":"systems",
            "action":"maxFuelLevelSet",
            "data": systems.tmpMaxFuelLevel
        }
        console.log("ToSend: ",JSON.stringify(tmpData))
        net.netPostAction(userId,tmpData);
    }
    // inpNumChange("astroTimeLeft")
    return (
        <div className={styles.systemsDiv}>
            <div className={styles.systemsHeader}>
                <p>Максимум топлива: {systems.maxFuelLevel}</p>
                <div className={styles.systemsHeaderInput}>
                        <FormControl style={{"fontSize":"20px"}} value={systems.tmpMaxFuelLevel} onChange={inpNumChange("tmpMaxFuelLevel")}/> 
                </div>
                <Button variant={"primary"} onClick={fuelMaxLevelSend}>
                    <span className={styles.systemsButtonText}>Установить</span>
                </Button>
            </div>   
        </div>    
    );
  })


export default GameSystems;