import React from 'react';
import styles from './users.module.css';
import { useStore } from "../../store/root_store";
import { observer } from "mobx-react-lite";

import {Button} from "react-bootstrap"

const UsersTable = observer(() => {
  let users=useStore().users.list;

  return(
    <>
      {Object.entries(users).map((el,idx)=>(
        <UsersTableElement 
          data={el[1]} 
          key={el[0]}
          uuid={el[0]}
          idx={idx} />
      ))}
    </>
)})
  
const UsersTableElement = ({idx,data,uuid}) => { 
    let backgroundColor = idx%2 ? "blue" : "gray";
    return (
      <div className={styles.tableRow}>
        <p className={`${styles[backgroundColor]} ${styles.idx}`}>{idx}</p>
        <p className={`${styles[backgroundColor]} ${styles.name}`}><b>NAME:</b> Client {data.name}</p>
        <p className={`${styles[data.admin ? "red" : backgroundColor]} ${styles.admin}`}><b>Admin:</b> {data.admin ? "Yes" : " No"}</p>
        <p className={`${styles[backgroundColor]} ${styles.services}`}><b>Services:</b> {data.menuList.map(el=>el+" ")}</p>
        <p className={`${styles[backgroundColor]} ${styles.uuid}`}><b>UUID:</b> {uuid}</p>
        {/* <Button 
          className={styles.menuElement}
          variant={statusButton}
          onClick={clbSwitcher(text)} 
        >
          <p className={styles.menuElementText}>{text}</p>
        </Button> */}
      </div>
    );
}

export default UsersTable;